<template>
  <div>
    <h2 class="text-center">QnA 수정</h2>
    <div>
      <table class="table table-boardered">
        <thead></thead>
        <tbody>
          <tr>
            <td>제목</td>
            <td>
              <input type="text" id="title" ref="title" v-model="qna.title" />
            </td>
          </tr>
          <tr>
            <td>내용</td>
            <td>
              <input
                type="text"
                id="content"
                ref="content"
                v-model="qna.content"
              />
            </td>
          </tr>
          <tr>
            <td colspan="2">
              <div class="text-center">
                <button class="btn btn-primary" @click="updateHandler">
                  수정
                </button>
                <button class="btn btn-primary" @click="moveHandler">
                  목록
                </button>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>
<script>
import http from "@/api/http";

export default {
  data() {
    return {
      qna: {},
    };
  },
  computed: {},
  created() {
    this.no = this.$route.query.no;
    http.get("qna/" + this.no).then(({ data }) => {
      this.qna = data;
    });
  },
  methods: {
    moveHandler() {
      this.$router.push({ name: "QnaList" });
    },
    updateHandler() {
      //데이타 검증
      let err = false;
      let msg = "";

      !this.qna.title &&
        ((msg = "제목을 입력해 주세요"),
        (err = true),
        this.$refs.title.focus());
      !err &&
        !this.qna.content &&
        ((msg = "내용을 입력해 주세요"),
        (err = true),
        this.$refs.content.focus());

      if (err) {
        alert(msg);
      } else {
        const _qna = {
          no: this.no,
          title: this.qna.title,
          content: this.qna.content,
          password: this.qna.password,
        };
        console.log(_qna);
        http
          .put("qna", _qna)
          .then(({ data }) => {
            if (data == "success") {
              alert("수정 성공");
              this.$router.push({
                name: "QnaDetail",
                query: { no: this.no },
              });
            }
          })
          .catch(({ response }) => {
            alert(response.data);
          });
      }
    },
  },
};
</script>
<style></style>
