<template>
  <div>
    <h2 class="text-center">QnA 등록</h2>
    <div>
      <table class="table">
        <thead></thead>
        <tbody>
          <tr>
            <td>제목</td>
            <td>
              <input type="text" id="title" ref="title" v-model="qna.title" />
            </td>
          </tr>
          <tr>
            <td>내용</td>
            <td>
              <input
                type="text"
                id="content"
                ref="content"
                v-model="qna.content"
              />
            </td>
          </tr>
          <tr>
            <td colspan="2">
              <div class="text-center">
                <button class="btn btn-primary" @click="createHandler">
                  등록
                </button>
                <button class="btn btn-primary" @click="moveHandler">
                  목록
                </button>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import http from "@/api/http";
export default {
  data() {
    return {
      qna: {
        title: "",
        content: "",
        password: null,
      },
    };
  },
  methods: {
    moveHandler() {
      this.$router.push({ name: "QnaList" });
    },
    createHandler() {
      let err = false;
      let msg = "";

      !this.qna.title &&
        ((msg = "제목을 입력해 주세요"),
        (err = true),
        this.$refs.title.focus());
      !err &&
        !this.qna.content &&
        ((msg = "내용을 입력해 주세요"),
        (err = true),
        this.$refs.content.focus());

      if (err) {
        alert(msg);
      } else {
        http
          .post("qna", this.qna)
          .then(({ data }) => {
            if (data == "success") {
              alert("등록 성공");
              this.moveHandler();
            }
          })
          .catch(({ response }) => {
            alert(response.data);
          });
      }
    },
  },
};
</script>

<style></style>
