import Vue from "vue";
import VueRouter from "vue-router";
import QnaView from "@/views/QnaView.vue";
import QnaList from "@/components/qna/QnaList.vue";
import QnaRegist from "@/components/qna/QnaRegist.vue";
import QnaDetail from "@/components/qna/QnaDetail.vue";
import QnaModify from "@/components/qna/QnaModify.vue";
Vue.use(VueRouter);

const routes = [
  {
    path: "/happyhouse/vueqna",
    name: "qnaView",
    component: QnaView,
    redirect: "/happyhouse/vueqna/qnalist",
    children: [
      {
        path: "qnalist",
        name: "QnaList",
        component: QnaList,
      },
      {
        path: "qnaregist",
        name: "QnaRegist",
        component: QnaRegist,
      },
      {
        path: "qnadetail",
        name: "QnaDetail",
        component: QnaDetail,
      },
      {
        path: "qnamodify",
        name: "QnaModify",
        component: QnaModify,
      },
    ],
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
