import Vue from "vue";
import Vuex from "vuex";
import http from "@/api/http";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    qnas: [],
    qna: {},
    searchs: [],
  },
  getters: {
    qnas(state) {
      return state.qnas;
    },
    searchs(state) {
      return state.searchs;
    },
    qna(state) {
      return state.qna;
    },
  },
  mutations: {
    setQnas(state, payload) {
      state.qnas = payload;
    },
    setSearchs(state, payload) {
      state.searchs = payload;
    },
    setQna(state, payload) {
      state.qna = payload;
    },
  },
  actions: {
    getSearchs({ commit, state }, payload) {
      let newQnas = [];
      if (payload.key == "번호") {
        for (let i = 0; i < state.qnas.length; i++) {
          if (state.qnas[i].no == payload.word) {
            newQnas.push(state.qnas[i]);
          }
        }
      } else if (payload.key == "작성자") {
        for (let i = 0; i < state.qnas.length; i++) {
          if (state.qnas[i].authorId.indexOf(payload.word) >= 0) {
            newQnas.push(state.qnas[i]);
          }
        }
      } else if (payload.key == "제목") {
        for (let i = 0; i < state.qnas.length; i++) {
          if (state.qnas[i].title.indexOf(payload.word) >= 0) {
            newQnas.push(state.qnas[i]);
          }
        }
      } else if (payload.key == "all") {
        newQnas = state.qnas;
      }
      commit("setSearchs", newQnas);
    },
    getQnas(context) {
      console.log("[action] getQnas......");
      http
        .get("qna")
        .then(({ data }) => {
          console.log("qnas......", data);
          context.commit("setQnas", data);
          context.commit("setSearchs", data);
        })
        .catch(({ response }) => {
          alert(response.data);
        });
    },
    getQna({ commit, state }, payload) {
      for (let i = 0; i < state.qnas.length; i++) {
        if (state.qnas[i].no == payload) {
          commit("setQna", state.qnas[i]);
        }
      }
    },
  },
  modules: {},
});
