<template>
  <div>
    <h2 class="text-center">QnA 상세</h2>
    <div>
      <table class="table table-boardered">
        <thead></thead>
        <tbody>
          <tr>
            <td>제목</td>
            <td v-text="qna.title"></td>
          </tr>
          <tr>
            <td>작성자</td>
            <td v-text="qna.authorId"></td>
          </tr>
          <tr>
            <td>내용</td>
            <td v-text="qna.content"></td>
          </tr>
          <tr v-if="qna.writerId != null">
            <td>답변자</td>
            <td v-text="qna.writerId"></td>
          </tr>
          <tr v-if="qna.answer != null">
            <td>답변</td>
            <td v-text="qna.answer"></td>
          </tr>
          <tr v-if="qna.answer == null">
            <td>답변</td>
            <td>
              <input
                type="text"
                id="answer"
                ref="answer"
                v-model="answer.answer"
              />
            </td>
          </tr>
          <tr v-if="qna.answer == null">
            <td></td>
            <td>
              <button class="btn btn-primary" @click="addAnswerHandler">
                작성
              </button>
            </td>
          </tr>
          <tr>
            <td colspan="2">
              <div class="text-center">
                <button class="btn btn-primary" @click="movePage">목록</button>
                <router-link :to="{ name: 'QnaModify', query: { no: qna.no } }">
                  <button class="btn btn-primary">수정</button>
                </router-link>
                <button class="btn btn-primary" @click="removeHandler">
                  삭제
                </button>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import http from "@/api/http";

export default {
  data() {
    return {
      qna: {},
      answer: {
        writerId: "",
        answer: "",
      },
    };
  },
  computed: {},
  created() {
    this.no = this.$route.query.no;
    http.get("qna/" + this.no).then(({ data }) => {
      this.qna = data;

      http.put("hit", this.no).catch(({ response }) => {
        alert(response.data);
      });
    });
  },
  methods: {
    movePage() {
      this.$router.push({ name: "QnaList" });
    },
    removeHandler() {
      console.log(this.qna.no);
      http
        .delete("qna/" + this.no)
        .then(({ data }) => {
          if (data == "success") {
            alert("삭제되었습니다");
            this.$router.push({
              name: "QnaList",
            });
          }
        })
        .catch(({ response }) => {
          alert(response.data);
        });
    },
    addAnswerHandler() {
      let err = false;
      let msg = "";
      !this.answer.answer &&
        ((msg = "답변 내용을 입력해 주세요"),
        (err = true),
        this.$refs.answer.focus());
      if (err) {
        alert(msg);
      } else {
        const _answer = {
          no: this.no,
          answer: this.answer.answer,
        };
        console.log(_answer);
        http
          .put("answer", _answer)
          .then(({ data }) => {
            if (data == "success") {
              alert("답변 작성 성공");
              this.$router.push({
                name: "QnaList",
              });
            }
          })
          .catch(({ response }) => {
            alert(response.data);
          });
      }
    },
  },
};
</script>

<style></style>
