<template>
  <div>
    <div class="row">
      <div class="col-2"></div>
      <div class="col-2">
        <select class="form-control" v-model="key">
          <option value="all">--선택하세요</option>
          <option v-for="(k, index) in keys" :value="k" :key="index">
            {{ k }}
          </option>
        </select>
      </div>
      <div class="col-4">
        <input class="form-control" type="text" v-model="word" />
      </div>
      <button class="col-2 btn btn-primary" @click="searchQna">검색</button>
      <div class="col-2"></div>
    </div>
    <div v-if="searchs.length > 0">
      <table class="table table-bordered table-hover">
        <colgroup>
          <col width="10%" />
          <col width="50%" />
          <col width="10%" />
          <col width="10%" />
          <col width="15%" />
        </colgroup>
        <thead>
          <tr>
            <th>No</th>
            <th>제목</th>
            <th>작성자</th>
            <th>조회수</th>
            <th>작성일</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="qna in searchs" :key="qna.no">
            <td v-text="qna.no"></td>
            <td>
              <router-link :to="'/happyhouse/vueqna/qnadetail?no=' + qna.no">{{
                qna.title
              }}</router-link>
            </td>
            <td v-text="qna.authorId"></td>
            <td v-text="qna.hit"></td>
            <td v-text="qna.regdate"></td>
          </tr>
        </tbody>
      </table>
    </div>
    <div v-else>등록된 질문이 없습니다.</div>
  </div>
</template>

<script>
import http from "@/api/http";
export default {
  data() {
    return {
      temp: [],
      searchs: [],
      word: "",
      keys: ["번호", "제목", "작성자"],
      key: "all",
    };
  },
  computed: {},
  created() {
    http
      .get("qna")
      .then(({ data }) => {
        this.searchs = data;
        this.temp = data;
      })
      .catch(({ reponse }) => {
        console.log(reponse.data);
      });
  },
  methods: {
    searchQna() {
      var newSearchs = [];
      for (var i = 0; i < this.temp.length; i++) {
        if (this.key == "번호" && this.word == this.temp[i].no) {
          newSearchs.push(this.temp[i]);
        } else if (
          this.key == "제목" &&
          this.word.indexOf(this.temp[i].title) >= 0
        ) {
          newSearchs.push(this.temp[i]);
        } else if (
          this.key == "작성자" &&
          this.word.indexOf(this.temp[i].authorId) >= 0
        ) {
          newSearchs.push(this.temp[i]);
        } else if (this.key == "all") {
          newSearchs.push(this.temp[i]);
        }
      }
      this.searchs = newSearchs;
    },
  },
};
</script>

<style></style>
